html, *, :before, :after {
  box-sizing: border-box;
}

body {
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.875;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (max-width: 479px) {
  .container {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

picture {
  margin: 0;
  padding: 0;
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --focus-pinc-color: #d41443;
  --accent-salmon-color: #ffa5ba;
  --text-black-color: black;
  --text-gray-color: #907e82;
  --focus-white-color: #fff;
  --main-transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --bcg-pink-color: #ffb8ca;
  --pink-btn-hover: #c7133e;
  --white-btn-hover: #ecebeb;
}

